import React, { Component } from 'react';
import Javascript from './portfolio/Javascript.png';
import CSS from './portfolio/CSS.png';
import HTML from './portfolio/HTML.png';
import Java from './portfolio/Java.png';
import JQuery from './portfolio/JQuery.png';
import ReactJs from './portfolio/ReactJs.png';
import Python from './portfolio/Python.png';
import MySQL from './portfolio/MySQL.png';
import MongoDB from './portfolio/MongoDB.png';
import Git from './portfolio/Git.png';
import Jump from 'react-reveal/Jump';
import Shake from 'react-reveal/Shake';
// import Flash from 'react-reveal/Flash'
import Pulse from 'react-reveal/Pulse';
import data from './portfolio/data.png';
import c from './portfolio/c.png';
import Computer_Networking from './portfolio/Computer-Networking.png'
// import  from './portfolio/';
// import  from './portfolio/';

var imgsObj = {
  Java,
  Javascript,
  HTML,
  CSS,
  Git,
  MongoDB,
  MySQL,
  Python,
  JQuery,
  ReactJs,
  "Computer Networking": Computer_Networking,
  "Data Structures & Algorithms":data,
  "C-programming": c
}
class Resume extends Component {
  render() {

    if(this.props.data){
      var skillmessage = this.props.data.skillmessage;
      var education = this.props.data.education.map(function(education){
        return <div key={education.school}><h3>{education.school}</h3>
        <p className="info">{education.degree} <span>&bull;</span><em className="date">{education.graduated}</em></p>
        <p>{education.description}</p></div>
      })
      var experience = this.props.data.experience.map(function(experience){
        return <div key={experience.company}><h3>{experience.company}</h3>
            <p className="info">{experience.title}<span>&bull;</span> <em className="date">{experience.duration}</em></p>
            <p style={{color:'black'}}>{experience.description}</p>
            <p>{experience.feeling}</p>

        </div>
      })
      var skillsSet1 = [];
      var skillsSet2 = [];
      let half = Math.floor(this.props.data.skills.length/2);
      for(var i = 0; i < half; i++){
        let skills = this.props.data.skills[i]
        skillsSet1.push(
          <Pulse>
               <div key={skills.name} style={{color:'black',display:'flex', alignItems:'center', width:'200px', justifyContent:'flex-start'}}>
                 <img src={imgsObj[skills.name]} alt="" style={{marginRight:4 }}/>
                 <em style={{fontSize:16, color:'black',fontFamily:"Helvetica, Arial, sans-serif"}}>{skills.name}</em>
               </div>
          </Pulse>
        )
      }
      for(var i = half; i < this.props.data.skills.length; i++){
        let skills = this.props.data.skills[i]
        skillsSet2.push(
          <Pulse>
               <div key={skills.name} style={{color:'black',display:'flex', alignItems:'center',  width:'200px', justifyContent:'flex-start'}}>
                 <img src={imgsObj[skills.name]} alt="" style={{marginRight:4 }}/>
                 <em style={{fontSize:16, color:'black',fontFamily:"Helvetica, Arial, sans-serif"}}>{skills.name}</em>
               </div>
          </Pulse>
        )
      }
      // var skills = this.props.data.skills.map(function(skills){
      //   // if(skills.name === 'Javascript'){
      //   //   return <li id="skilllist" key={skills.name} style={{color:'black'}}><img src={Javascript} alt="opppps"/> <em style={{fontSize:18}}>{skills.name}</em></li>
      //   // }else{
          
      //       return(
      //         <Pulse>
      //          <div key={skills.name} style={{color:'black',display:'flex', alignItems:'center', backgroundColor:'blue', width:'200px', justifyContent:'flex-start'}}>
      //            <img src={imgsObj[skills.name]} alt="" style={{marginRight:4 }}/>
      //            <em style={{fontSize:16, color:'black',fontFamily:"Helvetica, Arial, sans-serif"}}>{skills.name}</em>
      //          </div>
      //         </Pulse> 
      //       )
      //   // }
      // })
    }

    return (
      <section id="resume">

      <div className="row education">
         <div className="three columns header-col">
            <h1 style={{fontSize:20, color:'#313131'}}><span>Education</span></h1>
         </div>

         <div className="nine columns main-col">
            <div className="row item">
               <div className="twelve columns">
                 {education}
               </div>
            </div>
         </div>
      </div>


      <div className="row work" >

         <div className="three columns header-col">
            <h1 style={{fontSize:20, color:'#313131'}}><span>Experience</span></h1>
         </div>

         <div className="nine columns main-col" style={{color:'black', fontSize:18,textAlign:"justify",textJustify: "inter-word",fontFamily:"Helvetica, Arial, sans-serif"}}>
          {experience}
        </div>
    </div>



      <div className="row skill">

         <div >
            <h1 style={{fontSize:20, color:'#313131'}}><span>Skills</span></h1>
         </div>

         <div >

            <p style={{color:'	#585858', fontSize:18, fontFamily:"Helvetica, Arial, sans-serif"}}>{skillmessage}
            </p>

				
				  <div  style={{display:'flex', justifyContent:'space-around'}}>
					  <div style={{flex:'40%'}}> 
            {skillsSet1}
            </div>
            <div style={{flex:'40%'}}>
            {skillsSet2}
            </div>
					</div>
				
			</div>
      </div>
   </section>
    );
  }
}

export default Resume;
